
.loading-icon div {
  /* display: inline; */
  position: absolute;
  left: 8px;
  width: 16px;
  background: #000;
  animation: loading-icon .7s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.loading-icon div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.loading-icon div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.loading-icon div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes loading-icon {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}